        },
      ]
    }
  },
  methods: {
    changeScreening(e) {
      const target = e.target
      if (target.tagName !== 'SPAN') return
      const indicator = this.$refs.indicator
      const screenings = this.$refs.screenings
      const targetList = [...screenings.children]
      const index = targetList.indexOf(target)
      const length = targetList.length
      if (length <= 1) return
      const t1 = targetList[0]
      const t2 = targetList[1]
      const t1Left = t1.offsetLeft
      const t2Left = t2.offsetLeft
      const t1Width = t1.offsetWidth
      const distance = t2Left - t1Left - t1Width
      console.log(distance)

      indicator.style.left = `${index * 96}px`
      // this.title = target.innerText

      targetList.forEach(item => {
        item.classList.remove('selected')
      })
      target.classList.add('selected')
    }
  },
}
